@include tablet{
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog{
    margin-bottom: 2rem;
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type{
        margin-bottom: 0;
    }
}

.informacion-meta{
    span{
        color:$amarillo;
    }
}

.texto-entrada{
    a{
        color:$negro;
        margin-top: 2rem;
        display: block;
        @include tablet{
            margin-top:0
        }
    }
    
    h4{
        margin:0;
        line-height: 1.4;
        &::after{
            content:'';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
}

.testimonios{
    background-color: $verde;
    font-size: 2.4rem;
    color:$blanco;
    padding: 2rem;
    border-radius: 2rem;

    blockquote{
        padding-left: 5rem;
        position:relative;

        &::before{
            content:''; 
            background-image: url(../../../build/img/comilla.svg);
            display: block;
            width: 6rem;
            height: 8rem;
            background-repeat: no-repeat;
            position:absolute;
            left: -2rem;
        }
    }

    p{
        color:$blanco;
        text-align: right;
    }

}