.formulario{
    p{
        font-size: 1.4rem;
        color:$grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend{
        font-size:2rem;
        color: $grisOscuro;
    }

    label{
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }

    input:not([type="submit"]),textarea,select{
        display: block;
        padding:1rem;
        margin-bottom: 2rem;
        width: 100%;
        background-color: $blanco;
        border: 1.5px solid $gris;
        border-radius: 1rem;
    }

    textarea{
        height: 20rem ;
    }

    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="radio"]{
        margin:0;
    }
}