.derecha{
    @include tablet{
        display: flex;
        flex-direction: row;
        align-items: flex-end;
    }  
}
.dark-mode-boton{
    width: 2.2rem;
    filter: invert(100%);
    margin:0 .8rem;

    &:hover{
        cursor:pointer
    }

    @include tablet{
            margin:0 1rem;
            width: 3rem;
    }
}


.barra{
    display: flex;
    flex-direction: column;
    /* padding-top: 3rem; */
    

    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}


.navegacion{
    visibility: hidden;
    opacity:0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
    
    @include tablet{
        align-items: center;
        display: flex;
        visibility: visible;
    opacity: 1;
    height: auto;
    }

    a{
        color:$blanco;
        text-decoration: none;
        font-size: 1.8rem;
        display: block;
        text-align: center;
        
        &:hover{
            color: $verde;
        }
    @include tablet{
        margin-right: 2rem;

            &:last-of-type{
                margin-right:0;
            }
        } 
    }
}
.logo{
    width: 30rem;
}

.mobile-menu{
    width: 2rem;
    display: flex;
    justify-content: space-between;
    
    @include tablet{
    display: none;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}

.barra-logo{
    display: flex;
    justify-content: space-between;
    align-items: center;

    .mobile{
        display: flex;
        align-items: flex-end;
    }
}