.dark-mode{
    background-color: darken($grisOscuro,10);

    p{
        color:$blanco;
    }

    a,h1,h2,h3,h4{
        color:$gris;
    }

    .anuncio{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;

        .icono{
            filter: invert(100%);
        }
    }

    .resumen-propiedad{
        .icono{
            filter: invert(100%);
        }
    }

    .precio{
        color:$verde;
    }

    .formulario{
        legend,label,p{
            color:$gris;
        }

        input:not([type="submit"]),textarea,select{
            background-color: $grisOscuro;
            border:1px solid $grisOscuro;
            color:$blanco;
    }}

    td{
        color:$blanco;
    }
}