.alerta{
   color:$blanco;
   text-align: center;
   font-weight: $bold;
   text-transform: uppercase;
   padding: .5rem;
   margin:1rem 0;


 &.error{
    background-color: $rojo;
 }

 &.exito{
   background-color: $verde;
 }

}

table.propiedades, table.vendedores {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;

  thead{
    background-color: $verde;
      th{
        color:$blanco;
        padding: 2rem;
      }
}

tbody{
    td{
   text-align: center;
      padding: 1rem 4rem;
      border-bottom:  1px solid rgb(75, 75, 75);
      border-collapse: collapse;
    }
}
}

table.vendedores{
  thead{
    background-color: $azul;
  }
}

.imagen-tabla{
  width: 10rem;
}

.imagen-small{
  width: 20rem;
}