@include tablet{
    .contenedor-anuncios{
        display: grid;
        grid-template-columns: minmax(0, auto) minmax(0, auto) minmax(0, auto);
        grid-template-rows: minmax(0, auto);
        gap: 1rem;
    }


}

.contenido{
    height: 100%;
}

.anuncio{
    display: flex;
    flex-direction: column;
    background-color: lighten($gris, 5);
    border:1px solid darken($gris, 10);
    margin-bottom: 2rem;
    flex-grow: 1;
    
    
    .contenido-anuncio{
        display: flex;
        flex-direction: column;
        padding:2rem;
        flex-grow: 1;

    h3{
        margin: 1rem;
    }
    
   p, h3{
        flex-grow: 1;
    }
 }

    }
    
    .precio{
        color:$verde;
        font-weight: $bold;
        font-size: 3rem;
    }

.iconos-caracterisitcas{
    display: flex;
    padding: 0;
    list-style: none;
    max-width: 40rem;

        li{
            flex: 1;
            display: flex;

            img{
                flex: 0 0 3rem;
                margin-right: 1.5rem;
            }

            p{
                font-weight: $black;
            }
        }

}


.posicion{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}